.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  /* Custum CSS */
  
  .Appul {
    list-style: none;
  }
  
  .container {
    height: 100vw;
    padding: 20px;
    background: #83a4d4; /* fallback for old browsers */
    background: linear-gradient(to left, #b6fbff, #83a4d4);
    color: #171212;
  }
  
  .headlinePrimary {
    font-size: 48px;
    font-weight: 300;
    letter-spacing: 2px; 
  }
  
  .item {
    display: flex;
    align-items: center;
    padding-bottom: 5px; 
  }
  
  .item > span {
    padding: 0 5px;
    white-space: nowrap;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; 
  }
  
  .item > span > a {
    color: inherit; 
  }
  
  .button {
    background: transparent;
    border: 1px solid #171212;
    padding: 5px;
    cursor: pointer;
    transition: all 0.1s ease-in;
  }
  
  .button:hover > svg > g {
    fill: #ffffff;
    stroke: #ffffff; 
  }
  
  .buttonSmall {
    padding: 2px; 
  }
  
  .buttonLarge {
    padding: 10px; 
  }
  
  .searchForm {
    padding: 10px 0 20px 0;
    display: flex;
    align-items: baseline; 
  }
  
  .label {
    border-top: 1px solid #171212;
    border-left: 1px solid #171212;
    padding-left: 5px;
    font-size: 24px; 
  }
  
  .input {
    border: none;
    border-bottom: 1px solid #171212;
    background-color: transparent;
    font-size: 24px; 
  }